@-webkit-keyframes ballTransform {
	0% {
		-webkit-transform: translateY(0);
		transform: translateY(0); }
	25% {
		-webkit-transform: translateY(-15px);
		transform: translateY(-15px); }
	50% {
		-webkit-transform: translateY(0);
		transform: translateY(0); }
	75% {
		-webkit-transform: translateY(15px);
		transform: translateY(15px); }
	100% {
		-webkit-transform: translateY(0);
		transform: translateY(0); } }

@keyframes ballTransform {
	0% {
		-webkit-transform: translateY(0);
		transform: translateY(0); }
	25% {
		-webkit-transform: translateY(-15px);
		transform: translateY(-15px); }
	50% {
		-webkit-transform: translateY(0);
		transform: translateY(0); }
	75% {
		-webkit-transform: translateY(15px);
		transform: translateY(15px); }
	100% {
